  min-width: 20px;
  min-height: 20px;
  max-width: 20px;
  max-height: 20px;
  padding: 3px;
  border: solid 1px #777;
  border-radius: 5px;
  text-align: center;
  cursor: pointer; 
  background-color: #ffa;   
  background-size: cover;
}
.vuechessground {
  padding: 5px;
  border: solid 1px #777;
  width: 910px;
  position: relative;
  z-index: 10;
}
.bookdiv {
  margin-left: 10px;
  background-color: #fff;  
  height: 395px;
  width: 310px;
  overflow-y: scroll;
}
.bookdiv.running {
  background-color: #afa;
}
.bookmovesan {
  text-decoration: underline;
  color: #007;
  font-size: 20px;
  cursor: pointer;
  width: 60px;
}
.white {
  color: #070;
  font-family: monospace;
  font-weight: bold;
  width: 70px;  
}
.draw {
  color: #770;
  font-family: monospace;
  font-weight: bold;
  width: 70px;  
}
.black {
  color: #700;
  font-family: monospace;
  font-weight: bold;
  width: 70px;  
}
.greenbutton {
  background-color: #afa;
}
.bluebutton {
  background-color: #aaf;
}
.yellowbutton {
  background-color: #ffa;
}
.redbutton {
  background-color: #faa;
}
#disablediv {
  position: absolute;
  width: 0px;
  height: 0px;
  background-color: #ddd;
  opacity: 0.2;  
  z-index: 100;
}
#settingsdiv {
  position: absolute;  
  background-color: #f7f7f7;
  z-index: 100;
  padding: 15px;
  margin-left: 4px;
  height: 351px;
  border: solid 1px;
}
.trainvertcont {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.traincontrolsdiv {  
  display: flex;
  margin-bottom: 5px;
  border: solid 1px #777;
  padding: 6px;  
  align-items: center;
  height: 65px;
  min-width: 300px;  
  justify-content: space-around;  
}
.traininfo {
  display: inline-block;
  margin-right: 15px;
  margin-top: -5px;  
  background-color: #ffa;  
  font-weight: bold;
  padding: 5px;
  padding-left: 10px;
  padding-right: 10px;
  box-shadow: 3px 3px #777;
}
.trainerror {
  color: #f00;
  font-size: 25px;
}
.trainok {
  color: #070;
}
.trainrank {
  font-size: 25px;
  color: #070;
  font-weight: bold;
}
.trainexps {
  font-size: 20px;
  color: #007;
  font-weight: bold;
}
.trainrankdiv {
  margin-bottom: 5px;
}
.trainbutton {
  font-size: 20px;
  background-color: #dfd;
}
.stoptrainbutton {
  font-size: 15px;
  background-color: #fdd;
}
.makeyourmove {
  color: #070;
  font-size: 22px;
}
.settingupopeningpos {
  color: #777;
}
.histsan {
  animation: blinker 1s linear infinite;
}
.sancont {
  display: flex;
  align-items: center;  
}
.sanbutton {
  width: 12px;
  height: 12px;  
  margin: 1px;
  margin-left: 1px;  
  text-align: center;
  cursor: pointer;
  opacity: 0.3;
  padding: 1px;
}
.sanbutton.red {
  background-color: #faa;
}
.sanbutton.green {
  background-color: #afa;
}
.unrated {
  background-color: #fff;
}
.forcedloss {
  background-color: #f00;
  color: #fff;
  font-weight: bold;
}
.losing {
  background-color: #d00;  
  color: #fff;
  font-weight: bold;
}
.troll {
  background-color: #b00;
  color: #fff;
  font-weight: bold;
}
.dubious {
  background-color: #003;  
  color: #fff;
  font-weight: bold;
}
.experimental {
  background-color: #00a;
  color: #fff;
  font-weight: bold;
}
.stable {
  background-color: #00f;
  color: #fff;
  font-weight: bold;
}
.promising {
  background-color: #090;
  font-weight: bold;
}
.good {
  background-color: #0b0;
  font-weight: bold;
}
.winning {
  background-color: #0d0;
  font-weight: bold;
}
.forcedwin {
  background-color: #0f0;
  font-weight: bold;
}
